
import { defineComponent } from "vue";
export default defineComponent({
  name: "EditBar",
  props: {
    editBtn: {
      type: Boolean,
      default: true,
    },
  },
  setup(props, { emit }) {
    const clickClose = () => {
      emit("clickClose");
    };
    const clickEdit = () => {
      emit("clickEdit");
    };
    return {
      clickClose,
      clickEdit,
    };
  },
});
