<template>
  <div class="movement-comp">
    <div class="list">
      <div class="item scale">
        <div class="box">
          <img src="../../assets/image/ScenarioOne/scale.png" alt="">
        </div>
        <span>缩放</span>
      </div>
      <div class="item move">
        <div class="box">
          <img src="../../assets/image/ScenarioOne/move.png" alt="">
        </div>
        <span>移动</span>
      </div>
      <div class="item rotate">
        <div class="box">
          <img src="../../assets/image/ScenarioOne/rotate.png" alt="">
        </div>
        <span>旋转</span>
      </div>
    </div>
    <!-- 角色的详情编辑：服装和动画 -->
    <div class="role-box" v-if="status === 2">
      <edit-role></edit-role>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, watch } from 'vue'
import EditRole from "../editRole/index.vue"
export default defineComponent({
  name: "Movement",
  components: {
    EditRole
  },
  props: {
    status: {
      type: Number,
      required: false
    }
  },
  setup(props, { emit }) {
    console.log(999, props)
    let isEditRole = ref(false)
    let status = ref(-1)

    watch(
      () => props.status,
      (newVal, oldVal) => {
        status.value = newVal
      },
      {
        immediate: true,
        deep: true
      }
    )
    return {
      status,
      isEditRole
    }
  }
})
</script>

<style lang="scss" scoped>
  .movement-comp {
    position: absolute;
    top: 4rem;
    right: 15rem;
    display: flex;
    .list {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 2.7rem;
      .item {
        .box {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 2.25rem;
          height: 2.25rem;
          border-radius: 50%;
          background-image: url(../../assets/image/ScenarioOne/move-bg.png);
          background-repeat: no-repeat;
          background-size: 100% 100%;
        }
        img {
          width: 1rem;
          height: 1rem;
        }
        span {
          font-size: 12px;
          color: #fff;
          transform: scale(0.8);
          display: inline-block;
        }
      }
      .item.move {
        margin: 0.875rem 0;
      }
    }
  }
</style>