
import { defineComponent, ref } from "vue";
export default defineComponent({
  name: "EditRole",
  setup(props, { emit }) {
    let active = ref(0);
    let currentDressIndex = ref(-1);
    let progressValue = ref(55);
    let checked = ref(true);
    let currentAnimateIndex = ref(-1);
    let videoPlayFlag = ref(true);
    let videoStatus = ref(false);
    let audioStatus = ref(false);

    const clickDress = (idx: number) => {
      currentDressIndex.value = idx;
    };

    const onprogressChange = () => {};

    const clickAnimate = (idx: number) => {
      currentAnimateIndex.value = idx;
    };

    const playAnimateVideo = () => {
      let demoVideo: HTMLVideoElement = document.getElementById(
        "demoVideo"
      ) as HTMLVideoElement;
      videoPlayFlag.value = !videoPlayFlag.value;
      if (demoVideo?.paused) {
        demoVideo.play();
        if (checked.value) {
          demoVideo.muted = true;
        } else {
          demoVideo.muted = false;
        }
      } else {
        demoVideo.pause();
      }
    };

    const clickDemoVideo = () => {
      let demoVideo: HTMLVideoElement = document.getElementById(
        "demoVideo"
      ) as HTMLVideoElement;
      videoPlayFlag.value = true;
      demoVideo.pause();
    };

    const changeSwitch = () => {
      let demoVideo: HTMLVideoElement = document.getElementById(
        "demoVideo"
      ) as HTMLVideoElement;
      if (checked.value) {
        demoVideo.muted = true;
      } else {
        demoVideo.muted = false;
      }
    };

    const beforeChange = (arg: number) => {
      if (arg == 2) {
        // 点击的是close按钮
        emit("closeRoleModel");
        return false;
      }
      return true;
    };

    const playVideo = () => {}
    const playAudio = () => {}

    return {
      active,
      currentDressIndex,
      progressValue,
      checked,
      currentAnimateIndex,
      videoPlayFlag,
      videoStatus,
      audioStatus,
      clickDress,
      onprogressChange,
      clickAnimate,
      playAnimateVideo,
      clickDemoVideo,
      changeSwitch,
      beforeChange,
      playVideo,
      playAudio
    };
  },
});
